<template>
  <van-cell :title="title" :label="label" @click="toPlay">
    <template #right-icon>
      <div style="display: flex; align-items: center">
        <van-icon @click="toPlay" size="24" name="play-circle-o" />
      </div>
    </template>
  </van-cell>
</template>

<script>
export default {
  props: ["title", "label", "id"],
  methods: {
    toPlay() {
      // 跳转到播放页，并且携带id
      this.$router.push({
        name: "play",
        query: {
          id: this.id,
        },
      });
    },
  },
};
</script>

