// 专门封装跟home有关的接口
import request from "@/utils/request";

// 因为这里可能会封装多个接口，所以要用按需暴露

// 封装一个用来发获取推荐歌单的接口
// limit代表要查询出几条，传6就代表查6条
export const recommandList = limit => {
  // 这个函数里就发请求
  return request({
    url: "/personalized",
    params: {
      limit,
    },
  });

  // 这里不能写死.then，不然别人调用你这个函数
  // 拿到数据永远都是输出而已
  // .then (res => {
  //     console.log(res) // 处理res
  // })
};
// 封装一个专门获取最新音乐的接口
export const newMusicAPI = (params) => {
  return request({
      url: '/personalized/newsong?sb=' + Date.now(),
      params
  })
}
