import Vue from "vue";
import VueRouter from "vue-router";
// 导入组件
// @代表src目录
// 如果没写具体的文件名，他会找这个文件夹里的index.js，index.vue
import layout from "@/views/layout";
import play from "@/views/play";
import home from "@/views/home";
import search from "@/views/search";

Vue.use(VueRouter);
const routes = [
  // 路由重定向，就是让我输入a路径，可以自动跳转到b路径
  // 如果就只输入localhost:8080,自动跳转到 /layout/home 里
  { path: "/", redirect: "/layout/home" },
  {
    name: "layout",
    path: "/layout",
    component: layout,
    children: [
      {
        name: "home",
        path: "home",
        component: home,
        // 给路由存信息的地方（也可以理解为给路由打标记的地方）
        meta: {
          title: "首页",
        },
      },
      {
        name: "search",
        path: "search",
        component: search,
        meta: {
          title: "搜索",
        },
      },
    ],
  },
  { name: "play", path: "/play", component: play },
];
const router = new VueRouter({
  routes,
});

export default router;
