// 专门封装跟search有关的接口
import request from '@/utils/request'

// 封装一个获取热搜词汇的接口
export const hotWords = () => {

    return request({
        url: '/search/hot'
    })
}

// 封装一个搜索关键字得到结果的接口
export const search = (params) => {

    return request({
        url: '/search',
        params
    })
}