// const axiosB = axios.create() // 创建出一个新的axios对象
// 以后axiosA专门发a服务器，axiosB专门发B服务器

// 以上是为什么要这么做的架构思想，但实际我们本次案例，肯定只有一个接口
// 但是为了符合架构思想，方便扩展（万一以后本项目又多一个服务器，就可以用上了）

// 创建出一个新的axios对象，并设置新对象的基地址
const request = axios.create({
  baseURL: "http://music.zllhyy.cn/",
});

export default request;
